@import url(https://fonts.googleapis.com/css?family=Josefin+Slab:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.messageBox {
  background: #F3F3F3;
  border-radius: 20px;
  padding: 5px 20px;
  color: rgb(109, 103, 103);
  display: inline-block;
  max-width: 80%;
}

.messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 1.1em;
  word-wrap: break-word;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 1rem;
}

.sentText {
  display: flex;
  align-items: center;
  font-family: Helvetica;
  color: #504e4e;
  letter-spacing: 0.3px;
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}





.infoBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgb(196, 197, 199);
    border-radius: 4px 4px 0 0;
    height: 30px;
    width: 400px;
    min-width: 250px;
    padding: 10px 10px 10px 20px;
    margin-right: 10px;
    font-family: 'Josefin Sans', serif;
    font-size: 15px;
    color: rgb(15, 14, 14);
    background-color: #cdcfd3;
    border: 1px solid rgb(36, 31, 31);
    outline: none;
    border-radius: 20px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  }
  
  .leftInnerContainer {
    flex: 0.5 1;
    display: flex;
    align-items: center;
    margin-left: 5%;
    color: bl;
  }
  
  .rightInnerContainer {
    display: flex;
    flex: 0.5 1;
    justify-content: flex-end;
    margin-right: 5%;
  }
  
  .onlineIcon {
    margin-right: 5%;
  }
.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2px;
}

.chat-input {
    width: 300px;
    min-width: 250px;
    padding: 15px 10px 10px 20px;
    margin-right: 10px;
    font-family: 'Josefin Sans', serif;
    font-size: 15px;
    color: rgb(15, 14, 14);
    background-color: #cdcfd3;
    border: 1px solid rgb(36, 31, 31);
    outline: none;
    border-radius: 20px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    margin-top: 12rem;
    position: relative;
  }

  .buttons {
    display: flex;
    padding-left: 1rem;
    margin-top: 1rem;
    border: none;
    background-color: white;
    color: #fff !important;
    position: -webkit-sticky;
    position: sticky;
    position: relative;
  }
  
  .add-chat-btn {
    min-width: 100px;
    background-color: #262425;
    height: 25px;
    color: #ccc;
    border: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    outline: none;
    cursor: pointer;
    position: -webkit-sticky;
    position: sticky;
  }
.header h1 {
    color: rgb(104, 100, 100);
    font-weight: 300;
    text-align: center;
    margin: 50px 20px 60px 20px;
    font-family: 'Josefin Slab', serif;
  }
  .app-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1A1A1D;

  }
  
  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 8px;
    height: 60%;
    width: 35%;
  }

  @media (min-width: 320px) and (max-width: 480px) {
    .outerContainer {
      height: 100%;
    }
  
    .container {
      width: 100%;
      height: 100%;
    }
  }
  
  @media (min-width: 480px) and (max-width: 1200px) {
    .container {
      width: 60%;
    }
  }
